@font-face {
  font-family: "mypro-icon";
  font-weight: normal;
  font-style: normal;
  src: url("mypro-icon.eot");
  src: url("mypro-icon.eot?#iefix") format("embedded-opentype"),
    url("mypro-icon.woff") format("woff"),
    url("mypro-icon.ttf") format("truetype"),
    url("mypro-icon.svg#mypro-icon") format("svg");
}

.mypro-icon[class^="mypro-icon-"],
.mypro-icon[class*=" mypro-icon-"] {
  display: inline-block;
  font-family: "mypro-icon";
  font-size: 1rem;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: baseline;
}

